/* src/App.css */
.App {
  text-align: center;
}

section {
  padding: 50px;
  margin: 50px auto;
  /* Center the sections horizontally */
  max-width: 1200px;
  /* Set a maximum width for the sections */
  box-sizing: border-box;
  /* Include padding and border in the element's total width and height */
}

#about {
  background-color: #e0e0e0;
  padding-top: 60px;
  /* Adjust this value based on the height of your navbar */
}

#projects {
  background-color: #d0d0d0;
  padding-top: 60px;
  /* Adjust this value based on the height of your navbar */
}

#contact {
  background-color: #c0c0c0;
  padding-top: 60px;
  /* Adjust this value based on the height of your navbar */
}

/* Responsive design */
@media (max-width: 768px) {
  section {
    padding: 20px;
    margin: 20px auto;
  }
}